<script>
import {toRefs, reactive, onBeforeMount} from 'vue'
// import Common from '../hooks/common.js'
export default {
  name: "Index",
  setup() {
    const state = reactive({
      urlArr: {
        study: {
          out: 'http://117.159.24.46:3001/#/client/home',
          into: 'http://172.16.10.53:3001/#/client/home'
        },
        zhuPei: {
