    })
    // const {go} = Common()
    const goLogin = (data)=>{
      window.open(state.urlArr[data.url][state.currentNet])
    }
    onBeforeMount(() => {
      state.currentNet = window.location.hostname === '117.159.24.46' ? 'out' : 'into'
    })
    return {
      ...toRefs(state),
